@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Intro-container{display:flex;align-items:center;justify-content:center}.Intro-container .Intro-content{display:flex;flex-wrap:wrap;justify-content:center;width:61%;padding:3%;border-color:#4eabc580;border-width:2px;border-radius:5px;border-style:solid}.Intro-container .Intro-content .Intro-image{width:25%;height:100%;float:left}.Intro-container .Intro-content .Intro-text{align-self:center;padding-left:3%;width:50em;font-family:'Roboto', sans-serif;font-size:20px;line-height:30px;color:#1bceff}

.Portfolio-root-container{display:flex;flex-direction:column;width:100%;min-height:500px;color:#1bceff}.Portfolio-root-container h2{font-size:3em;width:100%;text-align:center}.Portfolio-root-container .Portfolio-root-content{display:flex;justify-content:center;width:100%;height:100%}.Portfolio-root-container .Portfolio-root-content .Portfolio-button-text{width:15em;height:100%;display:flex;align-items:center;justify-content:center;background-repeat:no-repeat;background-size:cover;font-size:48px}.Portfolio-root-container .Portfolio-root-content .Portfolio-button-text span{position:absolute;color:#1bceff;z-index:100;pointer-events:none}.Portfolio-root-container .Portfolio-root-content .Portfolio-button-text .Portfolio-button-image{transition:opacity 0.5s;min-height:150px;width:100%;display:flex;align-items:center;justify-content:center;background-repeat:no-repeat;background-size:cover;border-radius:20px;color:#1bceff;font-size:48px;cursor:pointer;opacity:0.3}.Portfolio-root-container .Portfolio-root-content .Portfolio-button-text .Games-image{background-image:url(/static/media/happy-hour.f894e7bc.png);margin:5px}.Portfolio-root-container .Portfolio-root-content .Portfolio-button-text .Soft-image{background-image:url(/static/media/programming.6031813b.jpg);margin:5px}.Portfolio-root-container .Portfolio-root-content .Portfolio-button-text .Portfolio-button-image:hover{transition:opacity 0.5s;opacity:1}.Portfolio-root-container .Portfolio-root-content .Portfolio-button-text.active .Portfolio-button-image{opacity:1}

.Portfolio-container{display:flex;flex-direction:column;width:100%;align-items:center;flex-grow:1}.Portfolio-container .Portfolio-content{display:flex;min-height:400px;width:75%;flex-wrap:wrap;justify-content:center}.Portfolio-container .Portfolio-content .Portfolio-element{display:flex;margin:5px;flex-direction:column;max-width:33em}.Portfolio-container .Portfolio-content .Portfolio-element .Portfolio-element-title{font-size:24px;align-self:center;margin-bottom:10px;margin-top:5px}.Portfolio-container .Portfolio-content .Portfolio-element .Portfolio-element-image{width:100%;max-height:300px;border-radius:10px;object-fit:cover}.Portfolio-container .Portfolio-content .Portfolio-element .Portfolio-element-year{font-size:20px;font-family:'Roboto', sans-serif;margin-top:15px;margin-bottom:5px;padding-left:10px;padding-right:10px}.Portfolio-container .Portfolio-content .Portfolio-element .Portfolio-element-description{font-size:20px;font-family:'Roboto', sans-serif;margin-bottom:15px;padding-left:10px;padding-right:10px}.Portfolio-container .Portfolio-content .Portfolio-element .Portfolio-element-buttons{display:flex;width:100%;height:100%;align-items:flex-end;justify-content:flex-end}.Portfolio-container .Portfolio-content .Portfolio-element .Portfolio-element-buttons .Portfolio-element-button{display:flex;width:100%;height:50px;align-items:center;justify-content:center;color:black;margin-top:5px;border-radius:10px;cursor:pointer;background-color:#1bceff;border-color:black;border-style:solid}.Portfolio-container .Portfolio-content .Portfolio-element .Portfolio-element-buttons .Portfolio-element-button span{-webkit-user-select:none;user-select:none}.Portfolio-container .Portfolio-content .Portfolio-element .Portfolio-element-buttons .Portfolio-element-button:hover{background-color:white}

@font-face{font-family:'tarrget';src:local("tarrget"),url(/static/media/tarrget.2b6beba0.ttf) format("opentype")}.App{min-height:100vh;height:100%;align-items:center;justify-content:center}html,body,#root,.App{height:100%;background-color:#282c34}.App-header{display:flex;flex-direction:column;align-items:center;font-size:calc(10px + 2vmin);color:white}.App-header h1{color:#1bf0ff;font-family:'tarrget', sans-serif;letter-spacing:5px;text-shadow:0 0 15px #1bceff,0 0 5px #ec1bff;-webkit-animation:text-shadow 4s infinite;animation:text-shadow 4s infinite}@-webkit-keyframes text-shadow{50%{text-shadow:0 0 40px #1bceff,0 0 25px #ec1bff}}@keyframes text-shadow{50%{text-shadow:0 0 40px #1bceff,0 0 25px #ec1bff}}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.Footer{width:100%;position:-webkit-sticky;position:sticky;bottom:0;height:50px;display:flex;align-items:flex-end;justify-content:center;min-height:20vh;pointer-events:none}.Footer .Footer-icons{pointer-events:all;display:flex;justify-content:center;align-items:center;background-color:#181a1f;height:60px;width:100%;color:white}.Footer .Footer-icons .Footer-icon{cursor:pointer;height:30px;margin-left:10px}

